@mixin dp {
	@media only screen and (max-width: "1199px") {
		@content; } }
@mixin d {
	@media only screen and (max-width: "1129px") {
		@content; } }
@mixin m {
	@media only screen and (max-width: "991px") {
		@content; } }
@mixin xs {
	@media only screen and (max-width: "767px") {
		@content; } }
@mixin xss {
	@media only screen and (max-width: "499px") {
		@content; } }


$white: #fff;
$orange: #f5a202;
$orange2: #f59102;
$orange3: #f56902;
$blue: #0177c2;
$blue-d: #0c2438;
$bg-blue: #e4eff7;
$black: #000;

@mixin rob {
	font-family: 'Roboto Condensed', sans-serif; }
@mixin play {
	font-family: 'Play', sans-serif; }

body {
	line-height: 1.2;
	@include play;
	font-size: 16px;
	color: $black;
	font-weight: 400; }
a {
	text-decoration: none; }
.title-lg {
	font-size: 84px;
	font-weight: 700;
	color: $white;
	@include rob;
	@include m {
		font-size: 40px; }
	@include xs {
		font-size: 24px; }
	span {
		color: $orange; } }
.title-m {
	font-size: 36px;
	font-weight: 700;
	position: relative;
	letter-spacing: -0.7px;
	@include m {
		font-size: 28px; }
	@include xs {
		font-size: 20px; }
	&--white {
		color: $white;
		// &:before
		// 	background: $white
		span {
			position: relative;
			&:before {
				content: '';
				position: absolute;
				left: 0;
				bottom: -3px;
				width: 20px;
				height: 2px;
				background: $white; } } }
	&--blue {
		color: $blue;
		&:before {
			content: '';
			position: absolute;
			left: 0;
			bottom: -3px;
			width: 20px;
			height: 2px;
			background: $blue; }
		p {
			position: relative;
			display: inline-block;
			vertical-align: top;
			&:before {
				content: '';
				position: absolute;
				left: 0;
				bottom: -3px;
				width: 20px;
				height: 2px;
				background: $blue; } }
		span {
			color: $black; } } }
.title-s {
	@include rob;
	line-height: 1;
	font-weight: 700;
	font-size: 24px;
	color: $white;
	@include m {
		font-size: 20px; }
	@include xs {
		font-size: 16px; } }
.article {
	p {
		margin-bottom: 20px;
		&:last-of-type {
			margin-bottom: 0; } } }
.two-col {
	@include clr;
	&__left {
		float: left;
		width: 50%; }
	&__right {
		float: right;
		width: 50%; } }
.btn {
	padding: 20px 60px;
	display: block;
	margin: 0 auto;
	background: $orange3;
	max-width: 380px;
	color: $white;
	border: 1px solid $orange3;
	box-sizing: border-box;
	@include trans;
	font-size: 24px;
	line-height: 1;
	font-weight: 700;
	&:hover {
		background: transparent;
		color: $orange3; } }
.btn-border {
	width: 227px;
	padding: 14px 27px;
	background: $white;
	border: 2px solid $orange3;
	color: $orange3;
	@include trans;
	font-weight: 700;
	font-size: 20px;
	text-align: center;
	margin: 0 auto;
	display: block;
	&:hover {
		color: $white;
		background: $orange3; }
	&--reverse {
		background: $orange3;
		color: $white;
		&:hover {
			color: $orange3;
			background: transparent; } } }
.field {
	resize: none;
	border: 1px solid $blue;
	background: none;
	display: block;
	width: 100%;
	margin-bottom: 20px;
	padding: 15px 35px;
	color: $black;
	font-size: 18px;
	line-height: 1;
	@include play;
	&--textarea {
		height: 90px;
		margin-bottom: 32px; } }
.is-hidden {
	overflow: hidden; }
